@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.session-card-wrapper {
  padding: 0 10px 0 0;
  border-radius: 5px;

  .sc-location {
    position: relative;

    .map-popup-container {
      display: none;
      position: absolute;
      min-width: 200px;
      height: 200px;
      width: 360px;
      border: 2px solid color.adjust(pick-color('primary', 'blue'), $lightness: 45%);
      border-radius: 10px;
      bottom: 100%;
      z-index: 200;
      left: 50%;
      transform: translateX(-50%);
      overflow: hidden;

      google-map {
        width: 100%;
        height: 100%;
      }
    }

    &:hover {
      .map-popup-container {
        display: block;
      }
    }
  }

  .sc-additional-icons {
    user-select: none;

    .dropdown-wrapper {
      width: fit-content;
      left: unset;
      right: 0;
    }
  }
}
